import React, { useState } from "react";
import styles from "./expertReview.module.css";

const ExpertReview = () => {
  const [reviews] = useState([
    {
      title: "   !",
      description:
        "                    .       .        . .",
      user: "** ",
    },
    {
      title: " ,  !",
      description:
        "          .          .           ~",
      user: "** ",
    },
    {
      title: "    !",
      description:
        "              .               .",
      user: "** ",
    },
  ]);

  return (
    <div className={styles.container}>
      <div className={styles.box}>
        <div className={styles.title}>  </div>
        <div className={styles.upper_box}>
          <div className={styles.rate_box}>
            <div className={styles.rate_label}></div>
            <div className={styles.rate}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                fill="rgb(53,197,240)"
                className="bi bi-star-fill"
                viewBox="0 0 16 16"
              >
                <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.283.95l-3.523 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z" />
              </svg>
              <span className={styles.number}>4.8</span>
            </div>
          </div>
          <div className={styles.review_box}>
            <div className={styles.review_label}></div>
            <span className={styles.review_text}>2,777</span>
          </div>
        </div>
        <div className={styles.card_box}>
          {reviews.map((item, index) => (
            <div className={styles.card} key={index}>
              <div className={styles.star_rate}>
                {[...Array(5)].map((n, idx) => (
                  <svg
                    key={idx}
                    xmlns="http://www.w3.org/2000/svg"
                    width="25"
                    height="25"
                    fill="rgb(53,197,240)"
                    className="bi bi-star-fill"
                    viewBox="0 0 16 16"
                  >
                    <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.283.95l-3.523 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z" />
                  </svg>
                ))}
              </div>
              <div className={styles.card_title}>{item.title}</div>
              <div className={styles.description}>{item.description}</div>
              <div className={styles.user}>{item.user}</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ExpertReview;
