import React, { useState, useEffect, useRef } from "react";
import styles from "./expertSlide.module.css";
import placeholder1 from "../../images/expert_image_1.png";
import placeholder2 from "../../images/expert_image_2.png";
import placeholder3 from "../../images/expert_image_3.png";
import placeholder4 from "../../images/expert_image_4.png";

const ExpertSlide = () => {
  const slideRef = useRef();
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [menu] = useState([
    " ",
    "",
    " ",
    "  ",
  ]);
  const handleClick = (index) => {
    setSelectedIndex(index);
  };

  let TOTAL_SLIDES = 4;
  const nextSlide = () => {
    if (selectedIndex + 1 >= TOTAL_SLIDES) {
      setSelectedIndex(0);
    } else {
      setSelectedIndex(selectedIndex + 1);
    }
  };
  const prevSlide = () => {
    if (selectedIndex === 0) {
      setSelectedIndex(TOTAL_SLIDES - 1);
    } else {
      setSelectedIndex(selectedIndex - 1);
    }
  };

  useEffect(() => {
    slideRef.current.style.transition = "all 0.5s ease-in-out";
    slideRef.current.style.transform = `translateX(-${selectedIndex}00%)`;
  }, [selectedIndex]);

  return (
    <div className={styles.container}>
      <div className={styles.box}>
        <div className={styles.menu}>
          {menu.map((item, index) => (
            <div
              key={index}
              className={
                selectedIndex === index
                  ? `${styles.item} ${styles.selected}`
                  : `${styles.item}`
              }
              onClick={() => {
                handleClick(index);
              }}
            >
              {item}
            </div>
          ))}
        </div>
        <div className={styles.image_container}>
          <div className={styles.image_window} ref={slideRef}>
            <div className={styles.image_div}>
              <div className={styles.text_box}>
                <div className={styles.title}>
                   <br />
                  
                </div>
                <div className={styles.description}>
                  , , , , A/S  <br />
                     <br />
                    
                </div>
              </div>
              <img
                className={styles.image}
                src={placeholder1}
                alt="placeholder"
              />
            </div>
            <div className={styles.image_div}>
              <div className={styles.text_box}>
                <div className={styles.title}>
                   <br />
                  
                </div>
                <div className={styles.description}>
                     , , , ,
                  <br />
                        
                  <br />
                      .
                </div>
              </div>
              <img
                className={styles.image}
                src={placeholder2}
                alt="placeholder"
              />
            </div>
            <div className={styles.image_div}>
              <div className={styles.text_box}>
                <div className={styles.title}>
                   0, <br />
                  
                </div>
                <div className={styles.description}>
                       <br />
                  ,      <br />
                    .
                </div>
              </div>
              <img
                className={styles.image}
                src={placeholder3}
                alt="placeholder"
              />
            </div>
            <div className={styles.image_div}>
              <div className={styles.text_box}>
                <div className={styles.title}>
                    <br />
                  ?
                </div>
                <div className={styles.description}>
                     <br />
                     . <br />
                     .
                </div>
              </div>
              <img
                className={styles.image}
                src={placeholder4}
                alt="placeholder"
              />
            </div>
          </div>
        </div>
        <div className={styles.row}>
          <div className={styles.prev} onClick={prevSlide}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              fill="black"
              classnames="bi bi-chevron-left"
              viewBox="0 0 16 16"
            >
              <path
                fillRule="evenodd"
                d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"
              />
            </svg>
          </div>
          <div className={styles.next} onClick={nextSlide}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              fillRule="black"
              classnames="bi bi-chevron-right"
              viewBox="0 0 16 16"
            >
              <path
                fillRule="evenodd"
                d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"
              />
            </svg>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExpertSlide;
