import React from "react";
import ShortcutItem from "../shortcutItem/shortcutItem";
import bed from "../../images/bed.png";
import shopping from "../../images/shopping-cart.png";
import house from "../../images/house.png";
import livingroom from "../../images/livingroom.png";
import smartphone from "../../images/smartphone.png";
import kitchen from "../../images/kitchen.png";
import book from "../../images/book.png";
import question from "../../images/question.png";
import styles from "./shortcuts.module.css";

const Shortcuts = (props) => {
  return (
    <div className={styles.container}>
      <ShortcutItem description={""} img={bed} />
      <ShortcutItem description={""} img={shopping} />
      <ShortcutItem description={""} img={house} />
      <ShortcutItem description={""} img={livingroom} />
      <ShortcutItem description={""} img={smartphone} />
      <ShortcutItem description={""} img={kitchen} />
      <ShortcutItem description={""} img={book} />
      <ShortcutItem description={""} img={question} />
    </div>
  );
};

export default Shortcuts;
